<template>
  <div>
    <el-upload
      ref="upload"
      multiple
      :disabled="uploadLoading"
      class="upload-demo"
      action=""
      :limit="3"
      :on-error="handleFileError"
      :on-success="handleFileSuccess"
      :before-upload="beforeFileUpload"
      :http-request="uploadFileFunc"
      :file-list="fileList"
      :on-change="handleChange"
      :on-exceed="videoExceed"
      :on-preview="previewVideo"
      :before-remove="videoRemove"
    >
      <el-button
        slot="trigger"
        size="small"
        type="primary"
        :disabled="fileList.length >= 3"
        :loading="uploadLoading"
      >
        上传视频
      </el-button>
      <div slot="tip" class="el-upload__tip" style="color: #F8747F;">
        温馨提示：视频上传后会在文章末尾依次展示，支持最多3个视频
      </div>
    </el-upload>
    <monitor ref="monitor" :monitor-info="monitorInfo" />
  </div>
</template>

<script>
import Monitor from '@/components/Monitor'

export default {
  name: 'VideoUploader',
  props: {
    uploadFileFunc: {
      type: Function,
      default: () => {}
    },
    videoRemove: {
      type: Function,
      default: () => {}
    },
    handleChange: {
      type: Function,
      default: () => {}
    },
    handleFileError: {
      type: Function,
      default: () => {}
    },
    uploadLoading: {
      type: Boolean,
      default: false
    },
    fileList: {
      type: Array,
      default: () => []
    },
    fileUrlList: {
      type: Array,
      default: () => []
    }
  },
  components: {
    Monitor
  },
  data() {
    return {
      monitorInfo: {}
    }
  },
  methods: {
    // 预览视频
    previewVideo(file) {
      const previewIndex = this.fileList.findIndex(
        targetfile => targetfile.uid === file.uid
      )
      this.monitorInfo = {
        type: 'video',
        meta: { url: this.fileUrlList[previewIndex].url }
      }
      this.$refs.monitor.show()
    },
    videoExceed() {
      this.$message.warning('最多上传三个文件')
    },
    handleFileSuccess() {
      this.$message({ message: '上传成功', type: 'success' })
    },
    beforeFileUpload(file) {
      const isSuccessType =
        [
          'video/mp4',
          'video/avi',
          'video/flv',
          'video/wmv',
          'video/mkv',
          'video/mov'
        ].indexOf(file.type) >= 0
      if (!isSuccessType) {
        this.$message.error('支持avi、flv、mp4、wmv、mkv、mov、等视频格式')
      }
      return isSuccessType
    }
  }
}
</script>
