<template>
  <div>
    <div class="step-container">
      <el-steps :active="active">
        <el-step title="选择服务对象" />
        <el-step title="编辑预防提醒内容" />
        <el-step title="预览发送" />
      </el-steps>
    </div>
    <div v-if="active === 0">
      <div class="choose-customer">
        <div class="search-container">
          <search-conditions ref="searchConditions" />
        </div>
        <baby-list @searchBabyList="searchBabyList" />
        <div class="next-step">
          <el-button type="primary" size="mini" @click="gotoStepTwo"
            >下一步</el-button
          >
        </div>
      </div>
    </div>
    <div v-if="active === 1 || active === 2" class="edit-container">
      <el-row>
        <el-col :span="6">
          <baby-info-area
            :page-type="'prevention'"
            :baby-id="
              currentTask.family_member
                ? currentTask.family_member._id
                : selectBaby._id
            "
          />
        </el-col>
        <el-col :span="18">
          <div class="edit-content">
            <el-row class="edit-section">
              <el-col class="type-bar" :span="1">
                <div
                  class="item-basic"
                  :class="[
                    currentTask.subType === 6 ? 'active-up' : 'forbidden-up'
                  ]"
                >
                  疫苗提醒任务
                </div>
                <div
                  class="item-basic"
                  :class="[
                    currentTask.subType !== 6 ? 'active-down' : 'forbidden-down'
                  ]"
                >
                  自定义任务
                </div>
              </el-col>
              <el-col v-show="active === 1" class="edit-form" :span="23">
                <div class="user-info">
                  <div class="user-info-label">
                    <el-avatar
                      :size="'small'"
                      :src="
                        currentTask.user
                          ? currentTask.user.avatar
                          : selectBaby.user_info && selectBaby.user_info.avatar
                      "
                    />
                    <div>&nbsp;&nbsp;用户:</div>
                  </div>
                  <div>
                    {{
                      currentTask.user
                        ? currentTask.user.nickname
                        : selectBaby.user_info && selectBaby.user_info.nickname
                    }}&nbsp;&nbsp;
                    <svg-icon v-if="isReplace" icon-class="replace" />
                  </div>
                </div>
                <div v-if="currentTask.subType === 6">
                  <vaccine-reference
                    :cur-vaccine="curVaccine"
                    :next-vaccine="nextVaccine"
                    :tips="'请根据知识点编辑此次给用户发送本次疫苗提醒的内容'"
                  ></vaccine-reference>
                </div>
                <send-content-form
                  ref="contentForm"
                  v-model="ruleForm"
                  :current-task="currentTask"
                  @preview="preview"
                  @handleSave="handleSave"
                  @handleClose="handleClose"
                />
              </el-col>
              <el-col v-if="active === 2" :span="23">
                <div class="preview">
                  <div class="preview__header">
                    <div class="avatar">
                      <el-avatar
                        :size="'small'"
                        :src="
                          currentTask.user
                            ? currentTask.user.avatar
                            : selectBaby.user_info &&
                              selectBaby.user_info.avatar
                        "
                      />
                    </div>
                    <div>
                      &nbsp;&nbsp;&nbsp;&nbsp;{{
                        currentTask.user
                          ? currentTask.user.nickname
                          : selectBaby.user_info &&
                            selectBaby.user_info.nickname
                      }}
                      &nbsp;&nbsp;
                      <svg-icon v-if="isReplace" icon-class="replace" />
                    </div>
                    <div>
                      &nbsp;&nbsp;&nbsp;&nbsp;宝宝：{{ previewBabyName }}
                    </div>
                  </div>
                  <send-content-preview
                    :rule-form="ruleForm"
                  ></send-content-preview>
                </div>
                <div class="preview-button-container">
                  <el-button @click="active--">返回修改</el-button>
                  <el-button type="primary" @click="sendTask">发 送</el-button>
                </div>
              </el-col>
            </el-row>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      :center="true"
      width="22%"
    >
      请选择要要发送预防提醒消息的宝宝后再进行下一步操作
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <task-close-dialog
      @closeTask="closeTask"
      @closeDialog="closeDialogVisible = false"
      :closeDialogVisible="closeDialogVisible"
    ></task-close-dialog>
  </div>
</template>

<script>
import BabyList from '@/components/BabyList'
import SendContentForm from '../components/SendContentForm'
import BabyInfoArea from '@/views/medical-service/diagnose/extend'
import SearchConditions from '../components/SearchConditions'
import SendContentPreview from '../components/SendContentPreview'
import VaccineReference from '../components/VaccineReference'
import TaskCloseDialog from '@/components/TaskCloseDialog'
import { mapGetters, mapMutations, mapActions } from 'vuex'
import {
  getTaskContent,
  updateTaskContent,
  sendTask,
  closeTask
} from '@/api/healthManagement'
import { localRead, localSave } from '@/utils'
import { getSimpleText } from '@/utils/healthManagement'

export default {
  name: 'Send',
  components: {
    BabyInfoArea,
    BabyList,
    SearchConditions,
    SendContentForm,
    SendContentPreview,
    VaccineReference,
    TaskCloseDialog
  },
  data() {
    return {
      // 疫苗任务详情表格变量
      closeDialogVisible: false,
      curVaccine: [],
      nextVaccine: [],
      isSaved: false,
      createTaskId: '',
      dialogVisible: false,
      ruleForm: {
        title: '',
        desc: '',
        delayReason: '',
        contentPic: '',
        contentTxt: '',
        contentVideos: []
      },
      checkList: [],
      active: 0,
      currentTask: {}
    }
  },
  computed: {
    ...mapGetters(['selectBaby', 'sendStatus']),
    closeRouterBabyId() {
      return this.currentTask.family_member
        ? this.currentTask.family_member._id
        : this.selectBaby._id
    },
    closeRouterTaskId() {
      return this.currentTask._id || this.createTaskId
    },
    isReplace() {
      return this.currentTask.is_replace
        ? this.currentTask.is_replace
        : this.selectBaby && this.selectBaby.is_replace
    },
    previewBabyName() {
      return this.currentTask.family_member
        ? this.currentTask.family_member.name
        : this.selectBaby.name
    }
  },
  created() {
    const taskStep = this.$route.query.step
    if (taskStep === 'send') {
      this.currentTask = JSON.parse(localRead('currentTask'))
      this.active = 1
      // 获取任务消息的详情
      getTaskContent(this.$route.query.taskId).then(res => {
        if (res.code === 20000) {
          this.ruleForm = res.data
          this.$refs.contentForm.fileList = res.data.contentVideos
          if (this.currentTask.subType === 6) {
            this.curVaccine = res.data.task_vaccine_schedule.curTask
            this.nextVaccine = res.data.task_vaccine_schedule.nextTask
          }
        }
      })
    } else {
      this.setSelectBaby(null)
      localSave('currentTask', null)
    }
  },
  methods: {
    ...mapActions({
      getBabyList: 'health/getBabyList'
    }),
    ...mapMutations({
      setSelectBaby: 'health/SET_SELECTBABY',
      setSendStatus: 'health/STE_SENDSTATUS'
    }),
    // 分页时查询宝宝列表
    searchBabyList() {
      this.$refs.searchConditions.search()
    },
    // 任务发送
    sendTask() {
      sendTask(this.createTaskId).then(res => {
        if (res.code === 20000) {
          this.$message.success('消息发送成功')
          setTimeout(() => {
            this.$router.back(-1)
          }, 3000)
        }
      })
    },
    // 保存图文消息
    saveTaskContent() {
      const params = {
        taskId: this.currentTask._id ? this.currentTask._id : this.createTaskId
      }
      const data = {
        title: this.ruleForm.title,
        content: this.ruleForm.content,
        contentPic: this.ruleForm.contentPic,
        contentTxt: this.getSimpleText(this.ruleForm.content),
        delayReason: this.ruleForm.delayReason,
        contentVideos: this.ruleForm.contentVideos
      }
      const subType = this.sendStatus === 'edit' ? this.currentTask.subType : 3
      return updateTaskContent(
        4,
        subType,
        this.currentTask.family_member
          ? this.currentTask.family_member._id
          : this.selectBaby._id,
        params,
        data
      )
    },
    // 点击保存按钮
    handleSave() {
      this.saveTaskContent().then(res => {
        if (res.code === 20000) {
          this.isSaved = true
          this.$message.success('保存成功')
          this.createTaskId = res.data._id
          this.isSaved = true
        }
      })
    },
    // 点击关闭任务
    handleClose() {
      if (!this.createTaskId && !this.currentTask._id) {
        this.$message.warning('任务还未创建')
        return
      }
      this.closeDialogVisible = true
    },
    closeTask(reasonForm) {
      // 请求取消任务的接口,取消成功后刷新任务列表
      const params = {
        reason: reasonForm.closeReasonType,
        reason_message: reasonForm.closeReason
      }
      const closeId = this.currentTask._id || this.createTaskId
      closeTask(closeId, params).then(res => {
        const operatorName = '关闭任务'
        this.$message({
          message:
            res && res.data.result === true
              ? operatorName + '成功'
              : operatorName + '失败',
          type: res && res.data.result ? 'success' : 'error'
        })
        this.closeDialogVisible = false
        setTimeout(() => {
          this.$router.push({
            path: 'close',
            query: {
              babyId: this.closeRouterBabyId,
              taskId: this.closeRouterTaskId
            }
          })
        }, 1000)
      })
    },
    // 预览图文消息
    preview() {
      if (!this.isSaved) {
        this.saveTaskContent().then(res => {
          if (res.code === 20000) {
            this.createTaskId = res.data._id
            this.active++
          } else {
            this.$message.error('保存失败')
          }
        })
      } else {
        this.active++
      }
    },
    // 从富文本中提取文本文字
    getSimpleText,
    gotoStepTwo() {
      if (!this.selectBaby) {
        this.dialogVisible = true
        return
      }
      this.active++
    }
  }
}
</script>
<style lang="scss">
/* 覆盖样式改变表格行高 */
.edit-section {
  .el-table__header tr,
  .el-table__header th {
    padding: 0;
    height: 50px;
  }
  .el-table__body tr,
  .el-table__body td {
    padding: 0;
    height: 50px;
  }
  .el-table--enable-row-hover .el-table__body tr:hover > td {
    background-color: #fff !important;
  }
}
</style>
<style scoped lang="scss">
@import '@/styles/healthManagement/addtask.scss';
</style>
