<template>
  <div class="reference">
    <div class="reference-label">参考知识点：</div>
    <div
      v-for="(reference, index) in vaccineList"
      :key="'con2' + index"
      class="reference-content"
    >
      <div class="text">
        {{ reference.desc }}
      </div>
      <div class="image-list">
        <el-row>
          <el-col
            v-for="(image, subIndex) in reference.icons"
            :key="'cur2' + subIndex"
            :span="2"
          >
            <img
              :src="image.url"
              width="50"
              height="50"
              @click="showImage(image, reference.icons)"
            />
          </el-col>
        </el-row>
      </div>
    </div>
    <!--图片显示器-->
    <monitor
      ref="monitor"
      :monitor-info="monitorInfo"
      :current-image-url-arr="curUrlList"
    />
  </div>
</template>

<script>
import Monitor from '@/components/Monitor'
export default {
  name: 'ReferenceList',
  components: {
    Monitor
  },
  props: {
    vaccineList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      monitorInfo: {},
      curUrlList: []
    }
  },
  methods: {
    // 查看图片详情
    showImage(image, urlList) {
      this.curUrlList = urlList.map(({ url }) => url)
      this.monitorInfo = {
        type: image.type,
        meta: { url: image.url }
      }
      this.$refs.monitor.show()
    }
  }
}
</script>

<style scoped lang="scss">
@import '@/styles/healthManagement/reference.scss';
</style>
